<template>
    <div>
        <div
            style="margin: 10px; padding: 10px; background-color: darkturquoise"
        >
            <h1>두인이네z</h1>
            <h3>vue로 만들었읍니다.</h3>
        </div>
        <input ref="inputEl1" type="hidden" value="이거슨 클립보드 복사여1" />
        <input ref="inputEl2" type="hidden" value="이거슨 클립보드 복사여2" />
        <button @click="handleCopy">복사하기</button>
        <button @click="handleCopy2">복사하기</button>
    </div>
</template>
<script>
import { ref } from "vue";

export default {
    name: "Home",
    data() {
        return {};
    },
    setup() {
        const inputEl1 = ref(null);
        const inputEl2 = ref(null);

        const handleCopyForIE = () => {
            inputEl1.value.setAttribute("type", "text");
            inputEl1.value.select();
            document.execCommand("copy");
            inputEl1.value.setAttribute("type", "hidden");
            alert("execCommand를 통해 내용이 복사되었습니다");
        };

        const handleCopy = () => {
            console.log("누름");
            if (inputEl1.value === null) {
                return;
            }
            if (!navigator.clipboard) {
                handleCopyForIE();
                return;
            }
            navigator.clipboard
                .writeText(inputEl1.value.value)
                .then(() => alert("clipboard API를 통해 내용이 복사되었습니다"))
                .catch((e) => {
                    console.log(e.message);
                    handleCopyForIE();
                });
        };

        const handleCopyForIE2 = () => {
            inputEl2.value.setAttribute("type", "text");
            inputEl2.value.select();
            document.execCommand("copy");
            inputEl2.value.setAttribute("type", "hidden");
            alert("execCommand를 통해 내용이 복사되었습니다");
        };

        const handleCopy2 = () => {
            console.log("누름");
            if (inputEl2.value === null) {
                return;
            }
            if (!navigator.clipboard) {
                handleCopyForIE2();
                return;
            }
            navigator.clipboard
                .writeText(inputEl2.value.value)
                .then(() => alert("clipboard API를 통해 내용이 복사되었습니다"))
                .catch((e) => {
                    console.log(e.message);
                    handleCopyForIE2();
                });
        };

        return {
            inputEl1,
            inputEl2,
            handleCopy,
            handleCopy2,
        };
    },
};
</script>
<style></style>
